* {
  color: black;
  font-family: "Times New Roman", Times, serif;
  /* border: 1px solid blue; */

}

body {
  margin: 0px;


}

header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 12px;
  justify-content: space-between;
  /* align-items: center; */
  background-image: url("./images/banner.jpg");
  background-position: auto;
  background-attachment: local;
}

header a {
  text-decoration: none;
  color: red;
  /* -webkit-text-stroke: 2px black; */
  /* align-items: flex-start; */
  

}

nav {
  justify-content: center;
}

nav ul {
  display: flex;
  flex-direction: row;
  list-style-type: none;
  align-items: right;
  justify-content: space-evenly;


}

nav ul li {
  padding: 12px;
  align-items: center;


} 

nav ul li a {
  font-size: 24px;
}


section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: auto;
  color: white;
  /* background-color: rgba(6, 4, 58, 0.887); */
  /* flex: 0 0 auto; */
  /* max-width: 67.5rem; */
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  padding-bottom: 1.875rem;
  padding-top: 3.5rem;
}

.flex-row {
  display: flex;
}

.section-box {
  width: auto;
  display: flex;
  flex-wrap: wrap;
  /* padding: 20px 50px 20px 50px;
}

.section-box a {
  width: auto;
  display: flex;

  /* padding: 20px 50px 20px 50px; */
}

.section-box a {
  text-decoration: none;
}

.section-box h3 {
  font-family: "Times New Roman", Times, serif;
}

.section-box img {
  max-width: 200px;
  height: 100px;
  padding: 1px;
}

.section-title {
  width: 120px;
  border-right: 5px solid black;
  padding: 20px;
  margin: 20px;
  display: flex;

}

.section-title h2 {
  width: 150px;
  font-size: 25px;
}

.section-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 2px;
  margin: 2px;
  justify-content: center;

  /* width: 75%; */

}

.sc-about {
  flex-direction: row !important;
  flex-wrap: nowrap;
}

.sc-about p {
  max-width: 600px
}

.section-card-c {

  padding: 10px;
  flex: 0 1 190px;
  width: auto;
  margin-bottom: 15px;
  justify-content: space-around;
}

.section-card {
  border-radius: 10px;
  padding: 10px;
  flex: 0 1 2190px;
  width: 100%;
  margin-bottom: 15px;
  justify-content: space-around;
}

.section-card header {
  padding: 2px;
}

.section-card p {
  text-align: center;
}



.projects {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 125px;
  background-size: cover;
  margin: 8px;
  border-radius: 12px;
}

.header-banner {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  /* width: 100%; */
  /* height: 150px; */
  background-size: cover;
  /* margin: 8px;
  border-radius: 12px; */
  
}

#run-buddy {
  background-image: url(./images/hero-bg.jpeg);
}

#recipe-finder {
  background-image: url(./images/sky.jpg);
}

#Byte-Farmers-Market {
  background-image: url(./images/brain.jpg);
}

#Portfolio {
  background-image: url(./images/circuit.jpg);
}

#Header {
  background-image: url(./images/banner.jpg);
}

/*Contact*/
.contact {
  flex: 0 0 auto;
  max-width: 67.5rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  padding-bottom: 1.875rem;
  padding-top: 3.5rem;
}

.contact-info {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.contact-info>* {
  flex: 1;
  margin: 15px;
}

.contact-info div {
  color: white;
  justify-content: center;
}

.contact-info h3 {
  font-size: 32px;
}

.contact-info p,
.contact-info address {
  margin: 20px 0;
  line-height: 1.5;
  font-size: 16px;
  font-style: normal;
}

.contact-form input,
.contact-form textarea {
  border: 1px solid #024e76;
  display: block;
  padding: 7px 15px;
  font-size: 16px;
  color: #024e76;
  width: 80%;
  margin-bottom: 15px;
  margin-top: 5px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.75);
}

.contact-form input:focus,
.contact-form textarea:focus {
  background-color: rgba(255, 255, 255, 1);
  outline: none;
}

.contact-form button {
  width: 100%;
  border: none;
  color: #024e76;
  text-align: center;
  padding: 15px 0;
  font-size: 16px;
  border-radius: 10px;
}

.contact-form button:hover {
  /* color: #fce138; */
  background: #39a6b2;
}

/*Contact End*/

/* Footer Start */
footer {
  flex: 0 0 auto;
  max-width: 67.5rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  padding-bottom: 1.875rem;
  padding-top: 3.5rem;
}

footer h2 {
  font-size: 30px;
  margin: 0;
}

footer div {
  line-height: 1.5;
  text-align: right;
}

footer h2,
footer div {
  text-align: center;
  width: 100%;
}

/* END FOOTER STYLES */

/* Profile Pic */
.profile-photo {
  max-width: 150px;
  padding: 10px;
  border-radius: 10%;
}

@media screen and (min-width: 699px) {
  .intro-container {
    justify-content: start;
    align-items: flex-start;
    text-align: left;
    padding: 10%;
    height: 25vh;
  }

  .intro-p {
    max-width: 450px;
  }
}